.timeline {
  position: relative;
  padding: 2em 0 0 0; }

.timeline::before {
  /* this is the vertical line */
  content: '';
  position: absolute;
  top: 0;
  left: 0.4em;
  height: 100%;
  width: 4px;
  background: rgba(0, 0, 0, 0.49); }

.timeline-img.post {
  background: #67b400; }

.timeline-block {
  position: relative;
  margin: 2em 0; }

.timeline-block:after {
  content: "";
  display: table;
  clear: both; }

.timeline-block:first-child {
  margin-top: 0; }

.timeline-block:last-child {
  margin-bottom: 0; }

.timeline-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  box-shadow: 0 0 0 4px white, inset 0 2px 0 rgba(0, 0, 0, 0.05), 0 3px 0 4px rgba(0, 0, 0, 0.35); }

.timeline-content {
  position: relative;
  margin-left: 2em;
  background: white;
  border-radius: 0.25em;
  padding: 1em;
  box-shadow: 0 3px 0 #d7e4ed;
  top: -1.5em; }

.timeline-content:after {
  content: "";
  display: table;
  clear: both; }

.timeline-content h2 {
  color: #303e49;
  margin-bottom: 0.5em; }

.timeline-content p {
  margin: 1em 0;
  line-height: 1.6; }

.post-date {
  position: absolute;
  width: 100%;
  left: -20%;
  top: 1.33em;
  float: left;
  font-size: 16px;
  font-size: 1rem; }
  @media screen and (max-width: 600px) {
    .post-date {
      position: relative;
      float: inherit;
      left: 0;
      top: 0; } }

.timeline-content.is-hidden,
.timeline-img.is-hidden {
  visibility: hidden; }

.timeline-content.bounce-in,
.timeline-img.bounce-in {
  visibility: visible;
  animation-name: bounceIn;
  animation-duration: .75s; }

@keyframes bounceIn {
  0%, 20%, 40%, 60%, 80%, 100% {
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3); }
  20% {
    transform: scale3d(1.1, 1.1, 1.1); }
  40% {
    transform: scale3d(0.9, 0.9, 0.9); }
  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03); }
  80% {
    transform: scale3d(0.97, 0.97, 0.97); }
  100% {
    opacity: 1;
    transform: scale3d(1, 1, 1); } }
@media screen and (max-width: 600px) {
  .gh-ribbon {
    display: none; } }
